.cat-info {
  justify-content: center;
  gap: 50px;
  margin-top: 50px;
  display: flex;
}

.cat-text {
  max-width: 600px;
}

.cat-text h1 {
  font-size: 40px;
}

.cat-text p {
  font-size: 22px;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.img-container {
  border-radius: 5px;
  box-shadow: 0 -5px 10px rgba(0, 0, 0, .5);
}

